@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Serif:400i|Open+Sans:400,700,800&display=swap");@custom-media --screen--xs-min screen and (min-width: 1px);@custom-media --screen--xs-max screen and (max-width: 767px);@custom-media --screen--xxs-max screen and (max-width: 415px);@custom-media --screen--sm-min screen and (min-width: 768px);:root {

  --vbig: 80px;
  --more-big: 68px;
  --big: 56px;
  --more-medium: 40px;
  --medium: 32px; 
  --more-small: 29px;
  --small: 18px;

  --vbig-size: var(--vgig);
  --medium-size: var(--medium);
  --small-size: var(--small);

  --rgb--main-green: 0, 128, 102;
  --rgb--green-light: 2, 159, 175;
  --rgb--blue: 104, 155, 239;
  --rgb--blue-dark: 3, 146, 192;
  --rgb--yellow: 252, 175, 38;
  --rgb--orange: 255, 123, 27;
  --rgb--black: 0, 0, 0;
  --rgb--white: 255, 255, 255;
  --rgb--gray: 122, 122, 122;

  --color--green: rgb(var(--rgb--main-green));
  --color--green-light: rgb(var(--rgb--green-light));
  --color--blue: rgb(var(--rgb--blue));
  --color--blue-dark: rgb(var(--rgb--blue-dark));
  --color--yellow: rgb(var(--rgb--yellow));
  --color--orange: rgb(var(--rgb--orange));
  --color--black: rgb(var(--rgb--black));
  --color--gray: rgb(var(--rgb--gray));
  --color--white: rgb(var(--rgb--white));
}html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}body {
  margin: 0;
  font-family: 'Open Sans' , Arial, Helvetica, sans-serif;
}main {
  display: block;
}h1 {
  margin: 0.67em 0;
  font-size: 2em;
}hr {
  overflow: visible;
  height: 0;
  box-sizing: content-box;
}pre {
  /* stylelint-disable-next-line font-family-no-duplicate-names */
  font-family: monospace, monospace;
  font-size: 1em;
}a {
  background-color: transparent;
}abbr[title] {
  border-bottom: 0;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}b,
strong {
  font-weight: bolder;
}code,
kbd,
samp {
  /* stylelint-disable-next-line font-family-no-duplicate-names */
  font-family: monospace, monospace;
  font-size: 1em;
}small {
  font-size: 80%;
}sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}sub {
  bottom: -0.25em;
}sup {
  top: -0.5em;
}img {
  border-style: none;
}button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}button,
input {
  overflow: visible;
}button,
select {
  text-transform: none;
}button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}fieldset {
  padding: 0.35em 0.75em 0.625em;
}legend {
  display: table;
  max-width: 100%;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  white-space: normal;
}progress {
  vertical-align: baseline;
}textarea {
  overflow: auto;
}[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}details {
  display: block;
}summary {
  display: list-item;
}template {
  display: none;
}[hidden] {
  display: none;
}*,
*::before,
*::after {
  box-sizing: border-box;
}html {
  height: 100%;
}body {
  min-width: 320px;
  min-height: 100%;
  background: #fff;
  color: #000;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 22px;
}h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}p {
  margin: 0;
}a {
  color: currentColor;
  outline: none;
  text-decoration: none;
}a:not([class]) {
  position: relative;
}button {
  border: 0;
  outline: none;
}button:hover {
    cursor: pointer;
  }img:not([class]),
svg:not([class]) {
  display: block;
  max-width: 100%;
}:global .root {
    overflow-x: hidden; 
    background-color: rgb(255, 255, 255); 
    background-color: var(--color--white);
  }:global .container {
    margin: 0 auto;
    max-width: 768px;
    min-height: 100vh;
    background-color: #fff;
    padding: 40px 20px 320px 20px;
  }:global .row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;    
  }:global .col {
    padding-left: 10px;
    padding-right: 10px;
  }:global .w100p {
    width: 100%;
  }:global .w50p {
    width: 50%;
  }:global .w33p {
    width: 33.33334%;
  }:global .w25p {
    width: 25%;
  }:global .mb--more-small {
    margin-bottom: 29px;
    margin-bottom: var(--more-small);
  }:global .mb--medium {
    margin-bottom: 32px;
    margin-bottom: var(--medium);
  }:global .mb--more-medium {
    margin-bottom: 40px;
    margin-bottom: var(--more-medium);
  }:global .mb--more-big {
    margin-bottom: 68px;
    margin-bottom: var(--more-big);
  }:global .mt--big {
    margin-top: 56px;
    margin-top: var(--big);
  }/* headers */:global h2 {
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    font-size: var(--medium);
    line-height: 32px;
    line-height: var(--medium);
    text-align: center;

    color: rgb(0, 0, 0);

    color: var(--color--black);
  }:global .headers > .italic,
  :global .headers.italic {
    font-family: 'IBM Plex Serif', serif;
    font-style: italic;
    font-weight: normal;
  }@media screen and (max-width: 767px) {

    :global .container {
      padding-bottom: 49px;
    }

    :global .mb--more-big-xs {
      margin-bottom: 68px;
      margin-bottom: var(--more-big);
    }

    :global .mb--more-medium-xs {
      margin-bottom: 40px;
      margin-bottom: var(--more-medium);
    }

    :global .list {
      position: relative;
      height: 298px;
      overflow-x: auto;
      overflow-y: hidden;
      margin-bottom: 10px;
      
    }

    :global .list > .row {
      flex-wrap: nowrap;
      position: absolute;
      left: 0;
      top: 8px;
      padding-bottom: 10px;
    }

    :global .list::-webkit-scrollbar {
      display: none;
    }
    
    :global .list {
      overflow: -moz-scrollbars-none;
    }
  }@media (--screen--xs-max) {

    :global .container {
      padding-bottom: 49px;
    }

    :global .mb--more-big-xs {
      margin-bottom: 68px;
      margin-bottom: var(--more-big);
    }

    :global .mb--more-medium-xs {
      margin-bottom: 40px;
      margin-bottom: var(--more-medium);
    }

    :global .list {
      position: relative;
      height: 298px;
      overflow-x: auto;
      overflow-y: hidden;
      margin-bottom: 10px;
      
    }

    :global .list > .row {
      flex-wrap: nowrap;
      position: absolute;
      left: 0;
      top: 8px;
      padding-bottom: 10px;
    }

    :global .list::-webkit-scrollbar {
      display: none;
    }
    
    :global .list {
      overflow: -moz-scrollbars-none;
    }
  }ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}
